<template>
 <div id="view-employee">
   <ul class="collection with-header">
    <li class="collection-header"><h4>{{name}}</h4></li>
    <li class="collection-item">id: {{id}}</li>
    <li class="collection-item">dept: {{dept}}</li>
    <li class="collection-item">position: {{position}}</li>
   </ul>
   <router-link to="/" class="btn grey">Back</router-link>
   <button @click="deleteEmployee" class="btn red">delete</button>
   <div class="fixed-action-btn">
    <router-link v-bind:to="{name: 'edit-employee' , params: {id: id}}" class="btn-floating">edit</router-link>
   </div>
 </div>
</template>

<script>
 import db from './firebaseInit'
 export default{
   name: 'view-employee',
   data() {
     return{
       id: null,
       name: null,
       dept: null,
       position: null
     }
   },
   beforeRouteEnter (to , from , next) {
     db.collection('employees')
     .where('employee_id' , '==' , to.params.id)
     .get()
     .then(querySnapshot => {
       querySnapshot.forEach(doc => {
         next(vm => {
           vm.id = doc.data().employee_id
           vm.name = doc.data().name
           vm.dept = doc.data().dept
           vm.position = doc.data().position
         })
       })
     })
   },
   watch: {
     '$route': 'fetchData'
   },
   methods: {
     fetchData () {
       db.collection('employees')
       .where('employee_id' , '==' , this.$route.params.id)
       .get()
       .then(querySnapshot => {
       querySnapshot.forEach(doc => {
           this.id = doc.data().employee_id
           this.name = doc.data().name
           this.dept = doc.data().dept
           this.position = doc.data().position
       })
     })
     },
     deleteEmployee () {
     if(confirm('Are u sure')){
       db.collection('employees')
       .where('employee_id' , '==' , this.$route.params.id)
       .get()
       .then(querySnapshot => {
       querySnapshot.forEach(doc => {
           doc.ref.delete()
           this.$router.push('/')
       })
     })
     }
   }
   }
 }
</script>

















